sform:rotateY(0)}.card__picture{background-size:cover;height:23rem;background-blend-mode:screen;-webkit-clip-path:polygon(0 0, 100% 0, 100% 85%, 0 100%);clip-path:polygon(0 0, 100% 0, 100% 85%, 0 100%);border-top-left-radius:3px;border-top-right-radius:3px}.card__picture--1{background-image:linear-gradient(to right bottom, #3e2c41, #261c2c)}.card__picture--2{background-image:linear-gradient(to right bottom, #393e46, #171010);-webkit-filter:grayscale(80%);filter:grayscale(80%)}.card__picture--3{background-image:linear-gradient(to right bottom, #232931, #5c5757)}.card__heading{font-size:2.8rem;font-weight:400;text-transform:uppercase;text-align:right;color:#fff;position:absolute;top:12rem;right:2rem;width:75%}.card__heading-span{padding:1rem 1.5rem;-webkit-box-decoration-break:clone;box-decoration-break:clone}.card__heading-span--1{background-image:linear-gradient(to top left, rgba(38,28,44,0.85), rgba(39,33,33,0.85))}.card__heading-span--2{background-image:linear-gradient(to top left, rgba(57,62,70,0.85), rgba(22,22,22,0.85))}.card__heading-span--3{background-image:linear-gradient(to top left, rgba(35,41,49,0.85), rgba(39,33,33,0.85))}.card__details{padding:3rem}.card__details ul{list-style:none;width:80%;margin:0 auto}.card__details ul li{text-align:center;font-size:1.5rem;padding:1rem}.card__details ul li:not(:last-child){border-bottom:1px solid #eee}.card__cta{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:90%;text-align:center}.card__price-box{text-align:center;color:#fff;margin-bottom:8rem}.card__price-only{font-size:1.4rem;text-transform:uppercase}.card__price-value{font-size:6rem;font-weight:100}
